.editable-cell {
    position: relative;
}

.editable-cell-value-wrap {
    padding: 5px 12px;
    cursor: pointer;
}

.editable-row {
    &:hover {
        .editable-cell-value-wrap {
            padding: 4px 11px;
            border: 1px solid #d9d9d9;
            border-radius: 2px;
        }
    }

    .ant-form-item-explain {
        position: absolute;
        top: 100%;
        font-size: 12px;
    }
}

@primary-color: #93C5FD;